.contacts{
  margin:0;
  position: relative;
  &__title{
    font-weight: 700;
    font-size: 25px;
    line-height: 30px;
    margin-top:38px;
    margin-bottom:0px;
  }
  &__item{
    font-size: 14px;
    line-height: 30px;
    display:flex;
    margin-top:28px;
    margin-bottom:0;
  }
  &__email-link{
    color:#444444;
    text-decoration: none;
    border-bottom: 1px solid  #62D1BA;
  }
  &__link{
    display: inline-block;
    padding:16px 0;
    text-align: center;
    border:2px solid #444444;
    width:260px;
    margin-bottom:39px;
    margin-top:42px;
    font-weight: 700;
    font-size: 18px;
    line-height: 20px;
    text-decoration: none;
    color:#444444;
  }
  &__map{
    margin-top:62px;
    margin-bottom:0px;
  }
  &__pin{
    position: absolute;
    bottom: 214px;
    left: 137px;
  }
}
